<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="开始时间：">
            <el-date-picker
              class="input-width"
              v-model="listQuery.startTime"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="请选择时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="结束时间：">
            <el-date-picker
              class="input-width"
              v-model="listQuery.endTime"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="请选择时间">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>
    <div class="table-container">
      <el-table ref="orderTable"
                :data="list"
                style="width: 100%;"
                v-loading="listLoading" border>
        <el-table-column label="编号" width="80" align="center">
          <template slot-scope="scope">{{scope.row.id}}</template>
        </el-table-column>
        <el-table-column label="订单编号" width="180" align="center">
          <template slot-scope="scope">{{scope.row.number}}</template>
        </el-table-column>
        <el-table-column label="桌号" width="180" align="center">
          <template slot-scope="scope">{{scope.row.tableNum}}</template>
        </el-table-column>
        <el-table-column label="提交时间" align="center">
          <template slot-scope="scope">{{scope.row.createDate}}</template>
        </el-table-column>
        <el-table-column label="订单金额" width="120" align="center">
          <template slot-scope="scope">￥{{scope.row.price}}</template>
        </el-table-column>
        <el-table-column label="订单状态" width="120" align="center">
          <template slot-scope="scope">{{scope.row.payStatus}}</template>
        </el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleViewOrderDetail(scope.$index, scope.row)"
            >查看订单详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="batch-operate-container" >

    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import {getAllOrderList, searchOrderListByTime} from "../../../api/order";

  const defaultListQuery = {
    startTime:null,
    endTime:null,
    pageNum: 1,
    pageSize: 5,
  };
  export default {
    name: "productCateList",
    data() {
      return {
        list: null,
        total: null,
        listLoading: true,
        listQuery:Object.assign({}, defaultListQuery),
        selectOrderList:null,
        parentId: 0
      }
    },
    created() {
      this.getList();
    },
    watch: {
      selectOrderList: function (newValue) {
        this.listQuery.startTime = newValue[1];
        this.listQuery.endTime=newValue[1];
      }
    },
    methods: {
      handleViewOrderDetail(index,row){
        this.$router.push({path:'/detail',query:{id:row.id}});
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getTimeList();
      },
      handleResetSearch() {
        this.selectOrderList=[];
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      getTimeList()
      {
        this.listLoading = true;
        searchOrderListByTime(this.listQuery.pageNum,this.listQuery.pageSize,
          this.listQuery.startTime,this.listQuery.endTime).then(response=>{
          this.listLoading = false;
          this.list = response.data.list;
          this.total = response.data.total;
        })
      },
      getList() {
        this.listLoading = true;
        getAllOrderList(this.listQuery.pageNum,this.listQuery.pageSize).then(response => {
          this.listLoading = false;
          this.list = response.data.list;
          this.total = response.data.total;
        });
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getList();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getList();
      },

    }
  }
</script>
<style scoped>
  .input-width {
    width: 203px;
  }
</style>





